import React, { Component } from 'react';
import BulkCreator from './components/BulkCreator';
import logo from './logo.svg';
import './App.css';

class App extends Component {
render() {
  return (
    <div className="page">
      <div className="ui container">
        <div className="page-heading">
          <h1 className="ui center aligned header">Shopify Proudct Metafiled Bulk Edit</h1>
        </div>
        <BulkCreator />
        <div className="ui center aligned container mb-20" style={{ fontSize: "20px" }}>Powered By <a href="https://www.madeforcode.com/">Made for Code</a></div>
        <div className="ui center aligned container" style={{ fontSize: "20px" }}>For help: <a target="_blank" href="https://form.jotform.com/200078604142445">Contact Us</a></div>
      </div>
      <div>
        <div className="mb-hidden" style={{position: "fixed",top: "40px",left: "30px"}}>
          <a href="/" class="ui labeled icon button" style={{boxShadow: "rgba(0, 0, 0, 0.18) 0px 0px 13px 3px"}}>
          <i class="left chevron icon"></i>Home</a>
        </div>
      </div>
    </div>
  );
}
}

export default App;



